import { Component, OnInit } from '@angular/core';
import { MatSnackBar, MatSnackBarConfig } from '@angular/material/snack-bar';
import { QuestionsService } from './services/questions.service';

interface Question {
  question: string;
  options: string[];
  correctAnswerIndex: number;
  imageUrl: string;
}

@Component({
  selector: 'app-quiz',
  templateUrl: './quiz.component.html',
  styleUrls: ['./quiz.component.scss'],
})
export class QuizComponent implements OnInit {
  private questions: Question[];
  private currentQuestionIndex = -1;
  private currentQuestion: Question;
  private answer = -1;

  constructor(
    private _questionService: QuestionsService,
    private _snackBar: MatSnackBar
  ) {}

  ngOnInit() {
    this.questions = this.getQuestions();
    this.updateCurrentQuestion();
  }

  getQuestions() {
    return this._questionService.getQuestions();
  }

  onSubmit() {
    const config: MatSnackBarConfig = {
      duration: 1000,
      verticalPosition: 'top',
    };
    if (this.answer === this.currentQuestion.correctAnswerIndex) {
      this._snackBar.open('Corretto!', '', config);
      this.updateCurrentQuestion();
    } else {
      this._snackBar.open('Sbagliato!', '', config);
    }
  }

  updateCurrentQuestion() {
    if (this.currentQuestionIndex < this.questions.length) {
      this.answer = -1;
      this.currentQuestionIndex++;
      this.currentQuestion = this.questions[this.currentQuestionIndex];
    }
  }
}
